import uniGrid from "@/components/uni-grid/uni-grid.vue";
import uniGridItem from "@/components/uni-grid-item/uni-grid-item.vue";
import uniIcon from "@/components/uni-icon/uni-icon.vue";

export default {
	components: {uniGrid, uniGridItem, uniIcon},
	data() {
		return {
			userIsLogin: false,
			userInfo: {}
		}
	},
	
	onShow() {
		// 使用挂载方法获取用户数据
		var userInfo = this.getGlobalUser("userInfo");
		// debugger;
		if (userInfo != null) {
			this.userIsLogin = true;
			this.userInfo = userInfo;
		} else {
			this.userIsLogin = false;
			this.userInfo = {};
			// 切换页面跳转，使用tab切换的api
			uni.navigateTo({
				url: "../login/login"
			});
		}
	},
	
	onLoad() {

	},
			
	
	methods: {
		dcRuntime() {
			// debugger;
			uni.navigateTo({
				url: "../dc/dc"
			})
		},
		
		dtuRuntime() {
			uni.navigateTo({
				url: "../dtu/dtu"
			})				
		},	
				
		csRuntime() {
			uni.navigateTo({
				url: "../cs/cs"
			})					
		},	
				
		trash() {
			uni.navigateTo({
				url: "../trash/trash"
			})					
		},
		
		setting() {
			uni.navigateTo({
				url: "../setting/setting"
			})	 				
		},
		
		dcMonth() {
			uni.navigateTo({
				url: "../dcMonth/dcMonth"
			})		
		},
		
		dcRange() {
			uni.navigateTo({
				url: "../dcRange/dcRange"
			})					
		},
		
		dtuMonth() {
			uni.navigateTo({
				url: "../dtuMonth/dtuMonth"
			})					
		},
		
		dtuRange() {
			uni.navigateTo({
				url: "../dtuRange/dtuRange"
			})					
		},
		
		csMonth() {
			uni.navigateTo({
				url: "../csMonth/csMonth"
			})					
		},
		
		csRange() {
			uni.navigateTo({
				url: "../csRange/csRange"
			})					
		},				
		
		
		
	}
}
